body {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.people {
  flex-wrap: wrap;
  gap: 40px 30px;
  margin: 30px auto;
  display: flex;
}

@media (width >= 500px) {
  .people {
    max-width: 430px;
  }
}

@media (width >= 1000px) {
  .people {
    max-width: 890px;
  }
}

.person {
  box-sizing: border-box;
  z-index: 0;
  width: 200px;
  transition: transform 1s, padding 1s, border-radius 1s;
  position: relative;
}

.person:hover {
  z-index: 1;
  background-color: #eee;
  border-radius: 12px;
  margin-bottom: -35px;
  padding: 12px;
  transition: transform 1s, padding 1s, border-radius 1s;
  position: relative;
  transform: scale(1.35);
}

.person:hover .person__photo {
  border-radius: 60px;
  transition: border-radius 1s;
}

.person:hover .person__name {
  margin-bottom: 10px;
}

.person:hover .person__extra {
  display: block;
}

.person__photo {
  border-radius: 50%;
  margin-bottom: 8px;
  transition: border-radius 1s;
  position: relative;
  overflow: hidden;
}

.person__photo-img {
  width: 100%;
  display: block;
}

.person__rank {
  user-select: none;
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 700;
  display: flex;
  position: absolute;
  top: 50%;
  right: 10px;
  overflow: hidden;
  transform: translateY(-50%);
}

.person__details {
  position: relative;
}

.person__name {
  text-align: center;
  color: #2f4f4f;
  width: 100%;
  font-size: 20px;
  font-weight: 700;
  display: inline-block;
}

.person__extra {
  text-align: center;
  display: none;
}
